                singleEntry. = tempArry[i].;\n                singleEntry. = tempArry[i].;\n\n                let obj = all_data_sorted.buylist[inputMonth].find(o => o. === singleEntry.);\n                if (obj) {\n                    if (Number.isNaN(parseFloat(obj.))) {\n                        singleEntry. = 0;\n                    } // if\n                    else {\n                        singleEntry. = parseFloat(obj.);\n                    } // else\n                } // if\n                else { // \n                    singleEntry. = 0;\n                } // else\n\n                singleEntry. = tempArry[i].;\n                singleEntry. = parseInt(tempArry[i].);\n                singleEntry. = singleEntry. - singleEntry.;\n                singleEntry. = 100 * (singleEntry.) / (singleEntry. / 26 * workdaysCount); // 26 is the average workdays in a month, as requested by Lyra Mao\n                singleEntry. = parseFloat(tempArry[i].);\n                singleEntry. = tempArry[i].;\n\n                resultArray.push(singleEntry);\n            } // for\n\n            // loop thru buylist and push to table\n            let alreadyPushed = false;\n            for (let i = 0; i < all_data_sorted.buylist[inputMonth].length; i++) {\n                let tempMonthRecord = all_data_sorted.buylist[inputMonth];\n                let singleEntry = {};\n                singleEntry. = tempMonthRecord[i].;\n                singleEntry. = tempMonthRecord[i].;\n                if (Number.isNaN(parseFloat(tempMonthRecord[i].))) {\n                    singleEntry. = 0;\n                } // if\n                else {\n                    singleEntry. = parseFloat(tempMonthRecord[i].);\n                } // else\n                singleEntry. = tempMonthRecord[i].;\n                let obj = all_data_sorted.outbound[inputMonth].find(o => o. === singleEntry.);\n                if (obj) {\n                    alreadyPushed = true;\n                } // if\n                else { // \n                    singleEntry. = 0;\n                } // else\n                singleEntry. = singleEntry. - singleEntry.;\n                singleEntry. = 100 * (singleEntry.) / (singleEntry. / 26 * workdaysCount); // 26 is the average workdays in a month, as requested by Lyra Mao\n                singleEntry. = parseFloat(tempMonthRecord[i].);\n                singleEntry. = tempMonthRecord[i].;\n\n                if (!alreadyPushed) {\n                    resultArray.push(singleEntry);\n                    alreadyPushed = false;\n                } // if\n            } // for\n        }; // SortCurrentMonthTable\n\n        const CalChartDatasets = async () => {\n            let tempNeedAmount = [];\n            let tempRealAmount = [];\n\n            let monthlyTemp = [];\n            for (let i = 0; i < 12; i++) { // loop thru whole year\n                monthlyTemp.splice(0); // clean up old records\n                await SortCurrentMonthTable(i, monthlyTemp); // get the sorted data by month\n                let currentMonthNeedTotalAmount = 0;\n                let currentMonthRealTotalAmount = 0;\n                // console.log(monthlyTemp); // test\n                for (let j = 0; j < monthlyTemp.length; j++) {\n                    if (monthlyTemp[j].) { // for safety measure\n                        if (checkedRows.length > 0) {\n                            if (checkedRows.find(o => o. == monthlyTemp[j].) != undefined) {\n                                currentMonthNeedTotalAmount += parseInt(monthlyTemp[j].);\n                                currentMonthRealTotalAmount += parseInt(monthlyTemp[j].);\n                            } // if\n                        } // if\n                        else {\n                            currentMonthNeedTotalAmount += parseInt(monthlyTemp[j].);\n                            currentMonthRealTotalAmount += parseInt(monthlyTemp[j].);\n                        } // else\n                    } // if\n                } // for\n\n                tempNeedAmount.push(currentMonthNeedTotalAmount);\n                tempRealAmount.push(currentMonthRealTotalAmount);\n            } // for\n\n            datasetNeed.value = tempNeedAmount;\n            datasetReal.value = tempRealAmount;\n        }; // CalChartDatasets\n\n        watch(checkedRows, async () => {\n            await CalChartDatasets();\n        }); // watch checked rows\n\n        watch(yearTag, async () => {\n            if (yearTag.value.toString().length == 4 && parseInt(yearTag.value) >= 1996) {\n                await triggerModal();\n\n                data.splice(0); // clean up old records\n\n                await getMats(yearTag.value);\n\n                $(\"body\").loadingModal(\"hide\");\n                $(\"body\").loadingModal(\"destroy\");\n            } // if\n        }); // watch year change\n\n        watch(monthTag, async () => {\n            // Jan is 0 in monthTag\n            await triggerModal();\n\n            data.splice(0); // clean up possible old records\n\n            // uncheck all checkboxes\n            document.querySelectorAll('.vtl-tbody-checkbox').forEach(el => el.checked = false);\n\n            if (document.querySelector(\".vtl-thead-checkbox\").checked) {\n                document.querySelector(\".vtl-thead-checkbox\").click();\n            } // if\n            checkedRows.splice(0);\n\n            monthStr.value = monthList.value[monthTag.value];\n            await SortCurrentMonthTable(monthTag.value, data);\n\n            $(\"body\").loadingModal(\"hide\");\n            $(\"body\").loadingModal(\"destroy\");\n        }); // watch month change\n\n        var all_data_sorted = {\n            buylist: [],\n            outbound: []\n        };\n        watch(mats, async () => {\n            await triggerModal();\n            all_data_sorted = {\n                buylist: [],\n                outbound: []\n            }; // clean up possible old records\n            data.splice(0); // clean up possible old records\n            if (mats.value == \"\") {\n                $(\"body\").loadingModal(\"hide\");\n                $(\"body\").loadingModal(\"destroy\");\n                return;\n            } // if\n\n            let allRowsObj = JSON.parse(mats.value);\n\n            // sort the yearly buylist\n            for (let i = 0; i < 12; i++) {\n                let newArray = allRowsObj.buylist.filter(function (el) {\n                    let sql_Date_To_JS_Date = new Date(el..replace(' ', 'T'))\n                    return sql_Date_To_JS_Date.getMonth() == i;\n                });\n\n                // sum the entries with the same \n                let sum_result = Object.values(newArray.reduce((acc, curr) => {\n                    let item = acc[curr.];\n\n                    if (item) {\n                        // set the current month demand to the latest entry\n                        if (new Date(curr.) > new Date(acc[curr.].)) {\n                            acc[curr.]. = parseFloat(curr.);\n                            acc[curr.]. = curr.;\n                        } // if\n                    } else {\n                        curr. = parseFloat(curr.);\n                        acc[curr.] = curr;\n                    } // if else\n\n\n                    return acc;\n                }, {}));\n\n                all_data_sorted.buylist[i] = sum_result;\n            } // for\n\n            // sort the yearly outbound\n            for (let i = 0; i < 12; i++) {\n                let newArray = allRowsObj.outbound.filter(function (el) {\n                    let sql_Date_To_JS_Date = new Date(el..replace(' ', 'T'))\n                    return sql_Date_To_JS_Date.getMonth() == i;\n                });\n\n                // sum the entries with the same \n                let sum_result = Object.values(newArray.reduce((acc, curr) => {\n                    let item = acc[curr.];\n\n                    if (item) {\n                        item. = parseInt(item.) + parseInt(curr.);\n                    } else {\n                        curr. = parseInt(curr.);\n                        acc[curr.] = curr;\n                    } // if else\n\n                    return acc;\n                }, {}));\n\n                all_data_sorted.outbound[i] = sum_result;\n            } // for\n\n            console.log(all_data_sorted); // test\n            await SortCurrentMonthTable(monthTag.value, data);\n            await CalChartDatasets();\n\n            $(\"body\").loadingModal(\"hide\");\n            $(\"body\").loadingModal(\"destroy\");\n            table.isLoading = false;\n        }); // watch for data change\n\n        const updateCheckedRows = (rowsKey) => {\n            checkedRows.length = 0;\n            checkedRows.push(...rowsKey);\n            // console.log(checkedRows); // test\n        };\n\n        return {\n            table,\n            searchTerm,\n            updateCheckedRows,\n            OutputExcelClick,\n            yearTag,\n            monthStr\n        };\n    }, // setup\n});\n</script>\n<style scoped>\n::v-deep(.vtl-table .vtl-thead .vtl-thead-th) {\n    color: #fff;\n    background-color: #196241;\n    border-color: #196241;\n}\n</style>"],"sourceRoot":""}]);
